export class DocumentGroupViewController extends BaseTreeViewController {
	constructor( options = {} ) {
		_.defaults( options, {
			el: '#document_group_view_container',



			tree_mode: null,
			grid_table_name: null,
			grid_select_id_array: null
		} );

		super( options );
	}

	init( options ) {
		//this._super('initialize', options );
		this.edit_view_tpl = 'DocumentGroupEditView.html';
		this.permission_id = 'document';
		this.viewId = 'DocumentGroup';
		this.script_name = 'DocumentGroupView';
		this.table_name_key = 'document_group';
		this.context_menu_name = $.i18n._( 'Document Groups' );
		this.grid_table_name = $.i18n._( 'Document Groups' );
		this.navigation_label = $.i18n._( 'Document Group' );

		this.tree_mode = true;
		this.primary_tab_label = $.i18n._( 'Document Group' );
		this.primary_tab_key = 'tab_document_group';

		this.api = TTAPI.APIDocumentGroup;

		this.grid_select_id_array = [];

		this.render();
		this.buildContextMenu();
		this.initData();
	}

	getCustomContextMenuModel() {
		var context_menu_model = {
			exclude: [
				'copy',
				'mass_edit',
				'delete_and_next',
				'save_and_continue',
				'save_and_next',
				'export_excel'
			],
			include: ['default']
		};

		return context_menu_model;
	}
}